import cartesian2d from '$ui/charts/utils/cartesian2d'

/**
 * 
 * @property {object} settings 
 * @property {string} [settings.direction=x] xy
 * @property {string|object|array} [label] position series.label
 * @property {string|object|array} [valueAxis] , echarts xAxisyAxis
 * @property {Object|String[]} [stack]  ['name1', 'name2']    {'name': ['name1', 'name2']}
 * @property {boolean|number} [smooth=0.5] 
 */
export const props = {
  // 
  direction: {
    type: String,
    default: 'x',
    validator(val) {
      return ['x', 'y'].includes(val)
    }
  },
  // position series.label
  label: [String, Object, Array],
  
  // 
  valueAxis: [String, Object, Array],
  
  //  ['name1', 'name2']    {'name': ['name1', 'name2']}
  stack: [Object, Array],
  
  // 
  bars: [String, Array],
  
  // 
  smooth: {
    type: [Boolean, Number],
    default: 0.5
  }
  
}


export default function (vm) {
  return cartesian2d(vm, 'line', props)
}
